@use 'base/mixins' as m;
@use 'base/variables' as v;

.plans{
    background-color: v.$secondary;

    h2{
        @include m.headingBorder(v.$white, v.$primary);
    }
    &__description{
        color: v.$white;
    }
}

.options{
    display: flex;
    flex-direction: column;
    @include m.tablet {
        flex-direction: row; 
        max-width: 80rem;
        margin-inline: auto;
        margin-block-start: 10rem;
    }

.plan{
    background-color: v.$white;
    flex-grow: 1;
    padding: 16rem 4rem 4rem 4rem;
    border-bottom: 1px solid #e1e1e1;
    text-align: center;

    @include m.tablet {
        border-bottom: unset;
        transition: 0.3s;
        &:hover{
            transform: scale(1.1);
        }
    }

    &__price{
        font-size: 1.8rem;
        text-align: center;
        font-weight: bold;
        text-transform: uppercase;
    }

    &__list{
        li{
            margin-bottom: 2rem;

            &:last-of-type{
                margin-bottom: 0
            }
        }
    }

    &__btn{
        @include m.btn(v.$primary , v.$white);
        border: 2px solid v.$white;
        display: block;
        margin-top: 1rem
    }

    &__start{
        @include m.iconoBg('../img/icono_inicio.svg');
    }
    &__pro{
        @include m.iconoBg('../img/icono_pro.svg');
    }
    &__premiun{
        background-color: v.$primary;
        order: -1;
        @include m.iconoBg('../img/icono_negocio.svg');
        @include m.tablet {
            order: unset;
            margin: -5rem 0;
            border-radius: 3rem;
            background-position: 50% 7rem;
            padding-top: calc(16rem + 5rem);
            z-index: 100;
        }
    }
}
   }
