@use 'base/mixins' as m;
@use 'base/variables' as v;


.header{

    @include m.bgImage('../img/header.jpg', v.$ligthGray);
    background-position: 80% 40%;
    padding: 5rem 0;
    
    &__logo{
        max-width: 20rem;
        margin: 0 auto;
        @include m.tablet {
            margin: 0;
        }
    }
    &__content{
        margin-top: 10rem;
        @include m.desktop {
            margin-top: 5rem;
        }
    }
    &__title{
        color: v.$white;
        font-size: 4.5rem;
        margin-bottom: 1rem;
        @include m.tablet {
            font-size: 6rem;
            max-width: 50%;
        }
        @include m.desktop {
            max-width: 40%;
        }
    }
    &__btn{
        @include m.btn(v.$primary , v.$white)
    }

}