@use 'base/mixins' as m;
@use 'base/variables' as v;

.footer{
    background-color: v.$secondary;
    

    &__grid{
        padding: 5rem 0;
        @include m.tablet {
            @include m.grid(4, 4rem) ;
        }
    }
    &__logo{
        max-width: 20rem;
        margin: 0 auto 5rem auto;
    }
    &__widget{
        margin-bottom: 2rem;

        @include m.tablet {
            margin-bottom: 0;
        }
        h3{
            color: v.$primary;
            text-align: left;
            margin-bottom: 3rem;
        }
    }

    &__nav{

        a{
            display: block;
            color: v.$white;
            margin-bottom: 1rem;
            transition: 0.3s;

            &:last-of-type{
                margin-bottom: 0;
            }

            &:hover{
                opacity: 0.5;
            }
        }
    }
    &__copyright{
        text-align: center;
        color: v.$white;
        margin: auto;
        border-top: 2px solid v.$primary;
        padding: 1rem;
        line-height: 1;
        max-width: 100rem;
        font-size: 1.2rem;
        text-transform: uppercase;
    }
}