body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  /*     &.body-scroll{
          padding-top: 8.6rem;
      } */
}
body.fix-body {
  overflow: hidden;
}

p {
  color: #000000;
  font-size: 1.6rem;
  line-height: 2;
}

a {
  text-decoration: none;
  color: #000000;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.container {
  width: min(90%, 120rem);
  margin: 0 auto;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
}
@media (min-width: 768px) {
  h2 {
    margin-top: 0;
  }
}
h2::after {
  content: "";
  background-color: #84d9bc;
  height: 0.7rem;
  width: 101%;
  display: block;
  margin: -1.4rem 0 0 0;
}

h3 {
  font-size: 3.2rem;
}

.section {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .section {
    padding: 8rem 0;
  }
}

.header {
  background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url("../img/header.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 80% 40%;
  padding: 5rem 0;
}
.header__logo {
  max-width: 20rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .header__logo {
    margin: 0;
  }
}
.header__content {
  margin-top: 10rem;
}
@media (min-width: 992px) {
  .header__content {
    margin-top: 5rem;
  }
}
.header__title {
  color: #FFFFFF;
  font-size: 4.5rem;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .header__title {
    font-size: 6rem;
    max-width: 50%;
  }
}
@media (min-width: 992px) {
  .header__title {
    max-width: 40%;
  }
}
.header__btn {
  background-color: #84d9bc;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: 0.3s ease;
  border: none;
}
.header__btn:hover {
  cursor: pointer;
  opacity: 0.5;
  color: gray;
}
@media (min-width: 768px) {
  .header__btn {
    width: auto;
    display: inline-block;
  }
}

@media (min-width: 768px) {
  .create__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    align-items: center;
  }
}
.create h2 {
  font-size: 3.6rem;
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
}
@media (min-width: 768px) {
  .create h2 {
    margin-top: 0;
  }
}
.create h2::after {
  content: "";
  background-color: #84d9bc;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.4rem 0 0 0;
}

.benefits {
  background-color: #84d9bc;
}
@media (min-width: 768px) {
  .benefits__grid {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 4rem;
    align-items: center;
  }
}
.benefits__content {
  grid-column: 1/4;
}
.benefits__content h2 {
  font-size: 3.6rem;
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
}
@media (min-width: 768px) {
  .benefits__content h2 {
    margin-top: 0;
  }
}
.benefits__content h2::after {
  content: "";
  background-color: #FFFFFF;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.4rem 0 0 0;
}
.benefits__img {
  grid-column: 4/6;
  transition: 0.3s;
}
.benefits__img:hover {
  transform: scale(1.1) rotate(4deg);
}

.statistics h2 {
  font-size: 3.6rem;
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
}
@media (min-width: 768px) {
  .statistics h2 {
    margin-top: 0;
  }
}
.statistics h2::after {
  content: "";
  background-color: #84d9bc;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.4rem 0 0 0;
}

.plans {
  background-color: #313c4e;
}
.plans h2 {
  font-size: 3.6rem;
  display: inline-block;
  margin-top: 5rem;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .plans h2 {
    margin-top: 0;
  }
}
.plans h2::after {
  content: "";
  background-color: #84d9bc;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.4rem 0 0 0;
}
.plans__description {
  color: #FFFFFF;
}

.options {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .options {
    flex-direction: row;
    max-width: 80rem;
    margin-inline: auto;
    margin-block-start: 10rem;
  }
}
.options .plan {
  background-color: #FFFFFF;
  flex-grow: 1;
  padding: 16rem 4rem 4rem 4rem;
  border-bottom: 1px solid #e1e1e1;
  text-align: center;
}
@media (min-width: 768px) {
  .options .plan {
    border-bottom: unset;
    transition: 0.3s;
  }
  .options .plan:hover {
    transform: scale(1.1);
  }
}
.options .plan__price {
  font-size: 1.8rem;
  text-align: center;
  font-weight: bold;
  text-transform: uppercase;
}
.options .plan__list li {
  margin-bottom: 2rem;
}
.options .plan__list li:last-of-type {
  margin-bottom: 0;
}
.options .plan__btn {
  background-color: #84d9bc;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: 0.3s ease;
  border: none;
  border: 2px solid #FFFFFF;
  display: block;
  margin-top: 1rem;
}
.options .plan__btn:hover {
  cursor: pointer;
  opacity: 0.5;
  color: gray;
}
@media (min-width: 768px) {
  .options .plan__btn {
    width: auto;
    display: inline-block;
  }
}
.options .plan__start {
  background-image: url("../img/icono_inicio.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
.options .plan__pro {
  background-image: url("../img/icono_pro.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
.options .plan__premiun {
  background-color: #84d9bc;
  order: -1;
  background-image: url("../img/icono_negocio.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
@media (min-width: 768px) {
  .options .plan__premiun {
    order: unset;
    margin: -5rem 0;
    border-radius: 3rem;
    background-position: 50% 7rem;
    padding-top: 21rem;
    z-index: 100;
  }
}

.testimonials {
  padding: 0 1rem;
  background-color: aliceblue;
}
@media (min-width: 768px) {
  .testimonials {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 4rem;
    align-items: center;
  }
  .testimonials__img {
    grid-column: 1/3;
  }
  .testimonials__content {
    grid-column: 3/6;
  }
}
.testimonials h2 {
  font-size: 3.6rem;
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
}
@media (min-width: 768px) {
  .testimonials h2 {
    margin-top: 0;
  }
}
.testimonials h2::after {
  content: "";
  background-color: #84d9bc;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.4rem 0 0 0;
}
.testimonials__text {
  background-color: #84d9bc;
  border: 0.5rem solid #313c4e;
  margin-bottom: 4rem;
  padding: 2rem;
  border-radius: 1rem;
  position: relative;
}
@media (min-width: 768px) {
  .testimonials__text {
    max-width: 70%;
  }
  .testimonials__text:nth-child(2) {
    margin-left: 10%;
  }
  .testimonials__text:nth-child(3) {
    margin-left: 20%;
  }
  .testimonials__text:nth-child(4) {
    margin-left: 30%;
  }
}
.testimonials__text::after {
  content: "";
  display: block;
  width: 5rem;
  height: 5rem;
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;
  top: 0;
}
.testimonials__text:last-of-type {
  margin-bottom: 0;
}
.testimonials__text p {
  margin: 0;
  padding-left: 6rem;
}

.footer {
  background-color: #313c4e;
}
.footer__grid {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 4rem;
  }
}
.footer__logo {
  max-width: 20rem;
  margin: 0 auto 5rem auto;
}
.footer__widget {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .footer__widget {
    margin-bottom: 0;
  }
}
.footer__widget h3 {
  color: #84d9bc;
  text-align: left;
  margin-bottom: 3rem;
}
.footer__nav a {
  display: block;
  color: #FFFFFF;
  margin-bottom: 1rem;
  transition: 0.3s;
}
.footer__nav a:last-of-type {
  margin-bottom: 0;
}
.footer__nav a:hover {
  opacity: 0.5;
}
.footer__copyright {
  text-align: center;
  color: #FFFFFF;
  margin: auto;
  border-top: 2px solid #84d9bc;
  padding: 1rem;
  line-height: 1;
  max-width: 100rem;
  font-size: 1.2rem;
  text-transform: uppercase;
}