@use 'base/mixins' as m;
@use 'base/variables' as v;

.benefits{
    background-color: v.$primary;
    &__grid{
        @include m.tablet {
            @include m.grid(5, 4rem) ;
            align-items: center;

        }
    }
    &__content{
        grid-column: 1/4;
        h2{
            @include m.headingBorder ( v.$secondary , v.$white)
        }
    }
    &__img{
        grid-column: 4/6;
        transition: 0.3s;
        &:hover{
            transform: scale(1.1) rotate(4deg);
        }
    }
}